.programs__wrapper {
  width: 87.5%;
  margin: 0 auto;
  margin-top: 22px;
}

.programs__item {
  background-color: #f2f2f2;
  margin-bottom: 20px;
  display: flex;
  align-items: flex-start;
  flex-direction: column;
  padding-left: 20px;
  padding-right: 20px;
}

.programs__title {
  margin-top: 0;
  margin-bottom: 0;
  font-size: 24px;
  line-height: 34px;
  font-weight: 400;
  text-transform: uppercase;
  padding-top: 31px;
  padding-left: 71px;
  padding-bottom: 20px;
  padding-right: 25px;
  position: relative;
}

.programs__title::before {
  position: absolute;
  content: "";
  width: 50px;
  height: 50px;
  background-color: #68b738;
  border-radius: 50%;
  top: 24px;
  left: 2px;
}

.programs__text {
  margin: 0;
  font-size: 14px;
  line-height: 18px;
  font-family: "Arial", sans-serif;
  padding: 11px 0 11px 0;
  margin-bottom: 11px;
  position: relative;
}

.programs__link {
  color: #000000;
  display: flex;
  font-size: 16px;
  line-height: 16px;
  text-transform: uppercase;
  margin-top: 20px;
  margin-bottom: 22px;
  padding-right: 40px;
}

.programs__text::after {
  position: absolute;
  content: "";
  width: 100%;
  height: 1px;
  background-color: #ececec;
  left: 0;
  bottom: -12px;
}
